@import url(https://fonts.googleapis.com/css?family=Titillium+Web&display=swap);
* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  height: 100%;
  width: 100%;
  font-family: 'Titillium Web';
}


body {
  background: #FFE4C4;
  background: white;
}

body.workout {
  background:#3F888F;
  background:white;
}

body.restout {
  background: #FFE4C4;
  background:white;
}


.App {
  text-align: center;
}

header {
  position: absolute;
  width: 100%;
  background-color: #4c5866;
  color: rgb(39, 38, 38);
  justify-content: space-between;
  height: 12vh;
  margin-top: -95px;
  /* padding: 0 4vw; */
}
header img {
  position: absolute;
  display: flex;
  width: 40px;
  /* margin-right: 650px; */
  margin-left: 30px;
  margin-top: 15px;
}
header i {
  position: absolute;
  display: flex;
  font-size: 28px;
  color: white;
  margin-top: 15px;
  margin-left: 80px;
}
header p {
  position: absolute;
  display: flex;
  justify-content: end;
  margin-left: 1100px;
  margin-top: 25px;
  color: white;

  font-size: 16px;
}

.TimeDisplay {
    position: relative;
}

.TimeDisplay svg {
    max-width: 300px;
}

.TimeDisplay circle {
    -webkit-transition: stroke-dashoffset 1s;
    transition: stroke-dashoffset 1s;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}

.TimeDisplay>div {
    position: absolute;
    left: 50%;
    top: 50%;
    display: grid;
    align-content: center;
    -webkit-transform: translate(-50%, -32px);
            transform: translate(-50%, -32px);
    text-align: center;
}

.TimeDisplay h1 {
    font-size: 52px;
    font-weight: 300;
    color: #D9534F;
    letter-spacing: 2px;
    margin: 0;
}

.TimeDisplay p {
    font-size: 14px;
    text-transform: uppercase;
    color: #bbb;
    letter-spacing: 3px;
    margin: -10px 0 0;
}


